import React, {Component} from 'react';
import axios from "axios";


class StarShipGrid extends Component {

    state = {
        next: null,
        previous: null,
        results: [],
        distance: 0,
        error: null
    };

    getPageData = (e) => {
        e.preventDefault();
        let id = e.target.id;
        let apiParams;
        if (id === "calculate") {
            apiParams = {
                endpoint: null,
                distance: this.state.distance
            };
        } else if (id === "next") {
            apiParams = {
                endpoint: this.state.next,
                distance: this.state.distance
            };
        } else {
            apiParams = {
                endpoint: this.state.previous,
                distance: this.state.distance
            };
        }


        axios.post("https://starwars-refuel-calculator.herokuapp.com/api/starship/refuels", apiParams)
            .then(response => {
                this.setState({
                    next: response.data.next,
                    previous: response.data.previous,
                    results: response.data.results,
                    error: null
                })
            }).catch(error => {
            this.setState({error: error.response.data.errors[0].defaultMessage});

        });
    };

    renderNextButton() {
        if (this.state.next != null) {
            return (
                <button onClick={this.getPageData} id="next"
                        style={{color: "black", backgroundColor: "yellow"}}>Next</button>
            );
        }
    };

    renderPreviousButton() {
        if (this.state.previous != null) {
            return (
                <button onClick={this.getPageData} id="previous"
                        style={{color: "black", backgroundColor: "yellow"}}>Previous</button>
            );
        }
    };

    handleDistanceChange = event => {
        this.setState({distance: event.target.value});
    };

    handleReturnKey = event => {
        if (event.key === 'Enter') {
            event.preventDefault();
            let apiParams = {
                endpoint: null,
                distance: this.state.distance

            };

            axios.post("https://starwars-refuel-calculator.herokuapp.com/api/starship/refuels", apiParams)
                .then(response => {
                    this.setState({
                        next: response.data.next,
                        previous: response.data.previous,
                        results: response.data.results,
                        error: null
                    });
                }).catch(error => {
                this.setState({error: error.response.data.errors[0].defaultMessage});

            });

        }
    };

    displayErrorMessage() {
        if (this.state.error != null) {
            return (
                <div style={{backgroundColor: "red", color: "white", textAlign: "center", marginLeft: "30%", marginRight: "30%",minWidth: "40%"}}>
                    {this.state.error}
                </div>);
        }
    };

    render() {

        return (
            <div style={{textAlign: "center", backgroundColor: "black"}}>
                <h3 style={{textAlign: "center", color: "yellow"}}>Enter The Distance To Calculate</h3>
                <form style={{textAlign: "center", color: "yellow"}}>

                    <input onKeyPress={this.handleReturnKey} onChange={this.handleDistanceChange} type="text"
                           name="distance"></input>
                    <button id="calculate" onClick={this.getPageData}
                            style={{color: "black", backgroundColor: "yellow"}}>Calculate
                    </button>
                    {this.displayErrorMessage()}
                </form>
                <table style={{
                    marginLeft: "15%",
                    width: "70%",
                    marginReft: "15%",
                    marginTop: "2%",
                    color: "yellow",
                    borderColor: "yellow",
                    border: "1px solid red"
                }}>
                    <tr>
                        <th style={{border: "1px solid white"}}>StarShip</th>
                        <th style={{border: "1px solid white"}}>MGLT</th>
                        <th style={{border: "1px solid white"}}>Consumables</th>
                        <th style={{border: "1px solid white"}}>Refuel Count</th>
                    </tr>
                    {this.state.results.map((item) => (
                        <tr>
                            <td>{item.name}</td>
                            <td>{item.MGLT}</td>
                            <td>{item.consumables}</td>
                            <td>{item.refuelCount}</td>
                        </tr>
                    ))}
                </table>
                <div style={{textAlign: "center", marginTop: "2%"}}>
                    {this.renderPreviousButton()}
                    {this.renderNextButton()}
                </div>
            </div>);
    }
};
export default StarShipGrid;